import React, { Component } from "react";
import { Link } from "react-router-dom";
import classes from "./images.module.css";
import img1 from "../../assests/images/1.jpg";
import img2 from "../../assests/images/2.jpg";
import img3 from "../../assests/images/3.jpg";
import img4 from "../../assests/images/4.jpg";
import img5 from "../../assests/images/5.jpg";
import img6 from "../../assests/images/6.jpg";
import img7 from "../../assests/images/7.jpg";
import img8 from "../../assests/images/8.jpg";
import img9 from "../../assests/images/9.jpg";
import img10 from "../../assests/images/10.jpg";
import img11 from "../../assests/images/11.jpg";
import img12 from "../../assests/images/12.jpg";
import img13 from "../../assests/images/13.jpg";
import Modal from "../../modal/Modal";

class Images extends Component {
  state = {
    modal: false,
  };
  constructor() {
    super();
    this.images = React.createRef();
    this.img1 = React.createRef();
    this.img2 = React.createRef();
    this.img3 = React.createRef();
    this.img4 = React.createRef();
    this.img5 = React.createRef();
    this.img6 = React.createRef();
    this.img7 = React.createRef();
    this.img8 = React.createRef();
    this.img9 = React.createRef();
    this.img10 = React.createRef();
    this.img11 = React.createRef();
    this.img12 = React.createRef();
  }
  // componentDidMount() {
  //   console.log(this.img1.current.getBoundingClientRect().top);
  //   console.log(this.img1.current.getBoundingClientRect().left);
  // }
  style = null;
  passId = null;
  getLocation = ele => {
    // console.log(ele.target.id);
    this.passId = ele.target.id;
    // console.log(ele.target.getBoundingClientRect().top);
    // console.log(ele.target.closest("#imgLayer"));
    ele.target.closest("#imgLayer").style.width = "100vw";
    ele.target.closest("#imgLayer").style.height = "100vh";
    ele.target.closest("#imgLayer").style.overflow = "hidden";

    this.setState({ modal: !this.state.modal });
  };
  changeLocation = ele => {
    this.setState({ modal: !this.state.modal });
    this.images.current.style = { overflow: "none" };
  };
  render() {
    return (
      <div
        className={classes.Images}
        ref={this.images}
        style={this.style}
        id="imgLayer"
      >
        <img
          className={classes.Img}
          src={img1}
          alt="a1"
          id="img1"
          ref={this.img1}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img2}
          alt="a2"
          id="img2"
          ref={this.img2}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img3}
          alt="a3"
          id="img3"
          ref={this.img3}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img4}
          alt="a4"
          id="img4"
          ref={this.img4}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img5}
          alt="a5"
          id="img5"
          ref={this.img5}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img6}
          alt="a6"
          id="img6"
          ref={this.img6}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img7}
          alt="a7"
          id="img7"
          ref={this.img7}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img8}
          alt="a8"
          id="img8"
          ref={this.img8}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img9}
          alt="a9"
          id="img9"
          ref={this.img9}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img10}
          alt="a10"
          id="img10"
          ref={this.img10}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img11}
          alt="a11"
          id="img11"
          ref={this.img11}
          onClick={ele => this.getLocation(ele)}
        />
        <img
          className={classes.Img}
          src={img12}
          alt="a12"
          id="img12"
          ref={this.img12}
          onClick={ele => this.getLocation(ele)}
        />
        {this.state.modal ? (
          <Modal changeLocation={this.changeLocation} id={this.passId} />
        ) : null}
      </div>
    );
  }
}
export default Images;
