import React, { Component } from "react";
import classes from "./Modal.module.css";
import { connect } from "react-redux";
class Modal extends Component {
  constructor() {
    super();
    this.nameRef = React.createRef();
    this.descRef = React.createRef();
  }
  state = {
    name: "",
    description: "",
  };
  componentDidMount() {
    this.setState({ name: this.newName, description: this.newDescription });
  }
  propagation = event => {
    event.stopPropagation();
  };
  changeValue = event => {
    if (this.nameRef.current == event.target) {
      this.setState({ ...this.state, name: event.target.value });
    }
    if (this.descRef.current == event.target) {
      this.setState({ ...this.state, description: event.target.value });
    }
  };
  newName = null;
  newDescription = null;
  render() {
    let state = this.props.data;
    // console.log(this.props.data);
    this.newName = this.props.data[this.props.id].name;
    this.newDescription = this.props.data[this.props.id].description;
    // console.log(this.newName);
    // console.log(this.newDescription);
    return (
      <div
        className={classes.Modal}
        onClick={ele => this.props.changeLocation(ele)}
      >
        <input
          className={classes.Input}
          type="text"
          placeholder="Enter Name"
          onClick={event => this.propagation(event)}
          value={this.state.name}
          onChange={event => this.changeValue(event)}
          ref={this.nameRef}
        />
        <br />
        <input
          className={classes.Input}
          type="text"
          placeholder="Enter Description"
          value={this.state.description}
          onChange={event => this.changeValue(event)}
          onClick={event => this.propagation(event)}
          ref={this.descRef}
        />
        <br />
        <button
          className={classes.Button}
          onClick={() =>
            this.props.sendData(
              this.props.id,
              this.nameRef.current.value,
              this.descRef.current.value
            )
          }
        >
          SEND
        </button>
      </div>
    );
  }
}
let stateToProps = state => {
  return {
    data: state,
  };
};
let propsToState = dispatch => {
  return {
    sendData: (id, nameRef, descRef) =>
      dispatch({ type: "CHANGEDATA", id: id, name: nameRef, desc: descRef }),
  };
};
export default connect(stateToProps, propsToState)(Modal);
